body{margin:0;width:100%;height:100%;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body::-webkit-scrollbar{width:0;height:0}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.Fotter{grid-area:fotter;background-color:#dfd1d1;z-index:3}.Fotter .container{max-width:1600px;margin:0 auto;padding:0 100px;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(auto, auto);font-size:17px;font-family:"Roboto"}@media screen and (max-width: 767px){.Fotter .container{padding:0 50px}}.Fotter .container .logo{margin:10px auto;color:#000;text-decoration:none}@media screen and (max-width: 3840px){.Fotter .container .logo{display:none}}@media screen and (max-width: 767px){.Fotter .container .logo{display:grid}}.Fotter .container .logo img{position:relative;left:40%}.Fotter .container .logo h4{margin:0}@media screen and (max-width: 3840px){.Fotter .container .info{display:grid;grid-template-columns:1fr 1fr}}@media screen and (max-width: 767px){.Fotter .container .info{display:grid;grid-template-columns:1fr}}.Fotter .container .info summary{display:block;font-size:20px;font-weight:600;border-bottom:1.5px solid rgba(0,0,0,.7176470588);margin:25px 10px 60px 10px;padding-bottom:1px;cursor:pointer;position:relative;color:#000}@media screen and (max-width: 767px){.Fotter .container .info summary{margin:25px 10px}}.Fotter .container .info summary::marker{font-size:0px}.Fotter .container .info summary::before{position:absolute;right:0;bottom:3px;content:"âˆŸ";transform:rotate(-45deg);color:rgba(0,0,0,.7176470588);transition:.7s all}.Fotter .container .info .contacts{text-align:start}.Fotter .container .info .contacts summary{text-align:center}.Fotter .container .info .contacts div div{margin:10px 10px;display:grid;grid-template-columns:auto 1fr;grid-gap:10px}.Fotter .container .info .pay{text-align:center;font-weight:200}.Fotter .container .info .pay p{margin:20px 0 0 0}.Fotter .container .info .pay img{width:70px;object-fit:cover;margin:10px 5%}.Fotter .container details[open] summary::before{transform:rotate(-225deg);bottom:-5px;color:rgba(0,0,0,.7176470588)}.Fotter .container details>summary::-webkit-details-marker{display:none}.Fotter .container .social{margin:20px auto}.Fotter .container .social a{margin:0 5px}.Fotter .container .bottom{display:grid;grid-template-columns:30px 1fr 30px;margin-bottom:15px}.Fotter .container .bottom span{grid-column:2/3;text-align:center;font-size:15px;font-weight:200}.Fotter .container .bottom img{height:30px;width:30px;border-radius:50%;cursor:pointer}@media screen and (max-width: 767px){.Fotter .container .bottom img{display:none}}.Fotter a img{width:30px}